.page-newsfeed {
  position: relative;

  .page-title {
    margin: 0;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__nav {
    display: flex;
    &-item {
      @extend .albums-nav__item;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;

    .news-feed__item {
      &:not(:last-child) {
        margin-bottom: 0;
      }
    }
  }

  .news-feed__media {
    height: 380px;
  }

  .news-feed__profile-ava {
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
    transition: opacity 0.3s ease-in-out;
    &:hover {
      opacity: 0.8;
    }
  }
}

@media (max-width: 1024px) {
  .page-newsfeed__grid {
    gap: 24px;
  }
  .page-newsfeed .news-feed__media {
    height: 320px;
  }
}

@media (max-width: 800px) {
  .page-newsfeed__grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  .page-newsfeed .news-feed__media {
    height: 480px;
  }
}

@media (max-width: 640px) {
  .page-newsfeed .news-feed__media {
    height: 320px;
  }

  .page-newsfeed__header {
    margin-bottom: 24px;
  }
}

@media (max-width: 480px) {
  .page-newsfeed .news-feed__media {
    height: 280px;
  }

  .page-newsfeed__header {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 16px;
  }

  .page-newsfeed .page-title {
    margin-bottom: 16px;
  }

  .page-newsfeed__nav {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
    &-item {
      width: 100%;
      display: flex;
      min-width: inherit;
      padding: 0 16px;
      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }
}

@media (max-width: 390px) {
  .page-newsfeed .news-feed__media {
    height: 240px;
  }

  .page-newsfeed .news-feed__item-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .page-newsfeed .news-feed__profile {
    margin-bottom: 12px;
  }

  .page-newsfeed .news-feed__options {
    width: 100%;
    .btn-favorite,
    .btn-folow {
      width: 100%;
    }
  }
}

@media (max-width: 375px) {
  .page-newsfeed .news-feed__media {
    height: 220px;
  }
}

@media (max-width: 375px) {
  .page-newsfeed .news-feed__profile-ava {
    width: 40px;
    height: 40px;
  }
}
